"use client"

import React, { useRef } from "react"
import { Canvas, useFrame } from "@react-three/fiber"
import * as THREE from "three"
import { Environment, Float, PresentationControls } from "@react-three/drei"

// Rotating ring group
function Rings() {
  const groupRef = useRef(null)

  useFrame((state) => {
    if (groupRef.current) {
      // Rotate the group of rings slowly over time
      groupRef.current.rotation.z = state.clock.getElapsedTime() * 0.1
    }
  })

  return (
    <group ref={groupRef}>
      {[...Array(3)].map((_, i) => (
        <mesh key={i} position={[0, 0, 0]}>
          <torusGeometry args={[2 + i * 0.8, 0.2, 16, 32]} />
          <meshStandardMaterial
            color={`hsl(${210 + i * 30}, 100%, 70%)`}
            metalness={0.5}
            roughness={0.2}
            emissive={`hsl(${210 + i * 30}, 100%, 50%)`}
            emissiveIntensity={0.2}
          />
        </mesh>
      ))}
    </group>
  )
}

// Simple upward-trending graph
function Graph() {
  // Generate a few Vector3 points
  const points = []
  for (let i = 0; i < 5; i++) {
    points.push(new THREE.Vector3(i - 2, Math.pow(1.3, i) - 2, 0))
  }

  // Build line geometry from our points
  const lineGeometry = new THREE.BufferGeometry().setFromPoints(points)

  return (
    <group position={[0, -1, 0]}>
      {/* The line connecting all points */}
      <line geometry={lineGeometry}>
        <lineBasicMaterial color="#60A5FA" linewidth={2} />
      </line>

      {/* Individual spheres at each point */}
      {points.map((point, i) => (
        <mesh key={i} position={[point.x, point.y, point.z]}>
          <sphereGeometry args={[0.1, 16, 16]} />
          <meshStandardMaterial color="#60A5FA" />
        </mesh>
      ))}
    </group>
  )
}

export function ThreeScene() {
  return (
    <div className="w-full h-full">
      <Canvas>
        {/* 
          PresentationControls: allows orbital-like controls, 
          plus snapping back to an initial orientation 
        */}
        <PresentationControls
          global
          rotation={[0.13, 0.1, 0]}
          polar={[-0.4, 0.2]}
          azimuth={[-1, 0.75]}
          config={{ mass: 2, tension: 400 }}
          snap={{ mass: 4, tension: 400 }}
        >
          {/* 
            Float: adds a gentle floating motion to the children 
            rotationIntensity & floatIntensity determine the movement 
          */}
          <Float speed={1.4} rotationIntensity={1} floatIntensity={2}>
            <Rings />
            <Graph />
          </Float>
        </PresentationControls>
        <Environment preset="city" />
        {/* Basic lighting */}
        <ambientLight intensity={0.5} />
        <pointLight position={[10, 10, 10]} intensity={0.5} />
      </Canvas>
    </div>
  )
}
